.main {
  background-color: #0F0F0F;
  width: 100vw;
  display: flex;
  margin: 0px;
}

.cards-container {
  display: flex;
  flex-wrap: wrap;
  flex-direction: row;
  width: 92%;
  height: 87%;
  gap: 15px;
  border-bottom-width: 3px;
  border-bottom-color: #3F3F3F;
  padding-bottom: 100px;
  margin-top: 135px;
  margin-left: 110px;
}

.card {
  width: 360px;
  height: 300px;
  display: flex;
  gap: 10px;
  flex-direction: column;
}

.card img {
  width: 100%;
  height: 202px;
  border-radius: 10px;
}

.card div {
  width: 100%;
  height: 100px;
  display: flex;
  flex-direction: row;
}

.card div .left {
  width: 55px;
}

.card div .left img {
  width: 36px;
  height: 36px;
  border-radius: 50%;
}

.card div .right {
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.card div .right h1 {
  font-family: roboto;
  font-weight: 500;
  font-size: 16px;
  color: #f1f1f1;
  margin: 0px;
  margin-bottom: 5px;
}

.card div .right p {
  font-family: roboto;
  font-weight: 400;
  font-size: 14px;
  color: #aaaaaa;
  margin: 0px;
}

@media only screen and (max-width: 1250px) {
  .cards-container {
    justify-content: center;
  }
  .card {
    width: 300px;
    height: 260px;
  }
}

@media only screen and (max-width: 1030px) {
  .card {
    width: 360px;
    height: 300px;
  }
}

@media only screen and (max-width: 880px) {
  .card {
    width: 300px;
    height: 260px;
  }
}

@media only screen and (max-width: 790px) {
  .cards-container {
    margin-left: 0px;
  }
  .card {
    width: 360px;
    height: 300px;
  }
}

@media only screen and (max-width: 400px) {
  .cards-container {
    width: 100%;
    gap: 30px;
  }
  .card {
    width: 335px;
    height: 270px;
  }
}
