.navbarmain {
  width: 100%;
  height: 115px;
  display: flex;
  position: fixed;
  flex-direction: column;
}

.navbar-box {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 55px;
  background-color: #0F0F0F;
}

.navbar-box .navbar-left {
  width: 24%;
  height: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding-left: 15px;
}

.navbar-box .navbar-left .menu {
  width: 35px;
  height: 35px;
}

.navbar-box .navbar-left .logo {
  width: 150px;
  height: 50px;
  object-fit: contain;
}

.navbar-box .navbar-middle {
  width: 52%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 8px;
}

.navbar-box .navbar-middle .search-box {
  width: 85%;
  height: 70%;
  display: flex;
  flex-direction: column;
}

.navbar-box .navbar-middle .search-box div {
  width: 100%;
  height: 99%;
  display: flex;
  flex-direction: row;
}

.navbar-box .navbar-middle .search-box div .search {
  display: flex;
  width: 90%;
  width: 100%;
  color: rgba(255, 255, 255, 0.88);
  background-color: #121212;
  font-size: 16px;
  padding-left: 20px;
  border-top-left-radius: 30px;
  border-bottom-left-radius: 30px;
  outline: 0px;
  border: 1px solid gray;
}

.navbar-box .navbar-middle .search-box div .search:focus {
  border: 1px solid #1877F2;
}

.navbar-box .navbar-middle .search-box div .search-icon {
  width: 15%;
  height: 97%;
  display: flex;
  justify-content: center;
  align-items: center;
  border-top-right-radius: 30px;
  border-bottom-right-radius: 30px;
  background-color: #222222;
  border: 1px solid gray;
  border-left: none;
}

.navbar-box .navbar-middle .search-box div .search-icon .icon {
  color: #ffffff;
}

.navbar-box .navbar-middle .search-box .suggestion-relative {
  width: 85%;
  height: 1%;
}

.navbar-box .navbar-middle .search-box .suggestion-relative .suggestions {
  display: flex;
  position: relative;
  z-index: 10;
  top: 8px;
  width: 100%;
  height: 400px;
  min-width: 300px;
  border-radius: 10px;
  background-color: #ffffff;
  margin-left: 10px;
  padding: 15px;
  flex-direction: column;
}

.navbar-box .navbar-middle .search-box .suggestion-relative .suggestions .suggest {
  display: flex;
  width: 100%;
  height: 40px;
  flex-direction: row;
}

.navbar-box .navbar-middle .search-box .suggestion-relative .suggestions .suggest .hahayst {
  height: 100%;
  width: 7%;
  display: flex;
  align-items: center;
}

.navbar-box .navbar-middle .search-box .suggestion-relative .suggestions .suggest .hahayst .re-search {
  width: 18px;
  height: 18px;
}

.navbar-box .navbar-middle .search-box .suggestion-relative .suggestions .suggest .search-history {
  width: 75%;
  height: 100%;
  margin: 0px;
  align-items: center;
  font-weight: 500;
}

.navbar-box .navbar-middle .search-box .suggestion-relative .suggestions .suggest .remove {
  height: 100%;
  width: 15%;
  margin: 0px;
  color: #1877F2;
  font-weight: 300;
  padding-right: 5px;
  align-items: center;
}

.navbar-box .navbar-middle img {
  display: flex;
  width: 45px;
  height: 45px;
}

.navbar-box .navbar-right {
  width: 24%;
  height: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
  align-items: center;
  gap: 20px;
  padding-right: 25px;
}

.navbar-box .navbar-right img {
  width: 45px;
  height: 45px;
  object-fit: contain;
}

.navbar-box .navbar-right .profile {
  width: 35px;
  height: 35px;
  object-fit: contain;
  border-radius: 50%;
}

.tab-container {
  width: 100%;
  height: 55px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 12px;
  background-color: #0F0F0F;
}

.tab-container .all {
  height: 60%;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #F1F1F1;
  margin-left: 85px;
  padding-left: 12px;
  padding-right: 12px;
}

.tab-container .all p {
  color: #000000;
  font-size: 14px;
}

.tab-container .others {
  height: 60%;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #222222;
  padding-left: 12px;
  padding-right: 12px;
}

.tab-container .others p {
  color: #f1f1f1;
  font-size: 14px;
}

.profile-container {
  width: 295px;
  height: 585px;
  z-index: 10;
  display: flex;
  position: fixed;
  top: 10px;
  right: 73px;
  background-color: #222222;
  border-radius: 10px;
  flex-direction: column;
}

.profile-container .profile-top {
  height: 75px;
  display: flex;
  flex-direction: row;
  padding: 15px;
  border-bottom: solid 1px gray;
}

.profile-container .profile-top .left {
  width: 20%;
  height: 100%;
  display: flex;
}

.profile-container .profile-top .left img {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.profile-container .profile-top .right {
  width: 80%;
  height: 100%;
  display: flex;
  flex-direction: column;
  gap: 3px;
}

.profile-container .profile-top .right .normal {
  font-family: arial;
  font-size: 16px;
  color: #f1f1f1;
  margin: 0px;
}

.profile-container .profile-top .right .blue {
  margin: 0px;
  font-family: arial;
  color: #1877F2;
  font-size: 14px;
  margin-top: 10px;
}

.profile-container .profile-middle {
  height: 125px;
  display: flex;
  flex-direction: column;
  padding: 15px;
  gap: 20px;
  border-bottom: solid 1px gray;
}

.profile-container .profile-middle p {
  font-family: arial;
  font-size: 14px;
  color: #f1f1f1;
  margin: 0px;
}

.profile-container .profile-middle-2 {
  height: 55px;
  display: flex;
  flex-direction: column;
  padding: 15px;
  gap: 20px;
  border-bottom: solid 1px gray;
}

.profile-container .profile-middle-2 p {
  font-family: arial;
  font-size: 14px;
  color: #f1f1f1;
  margin: 0px;
}

.profile-container .profile-middle-3 {
  height: 160px;
  display: flex;
  flex-direction: column;
  padding: 15px;
  gap: 20px;
  border-bottom: solid 1px gray;
}

.profile-container .profile-middle-3 p {
  font-family: arial;
  font-size: 14px;
  color: #f1f1f1;
  margin: 0px;
}

.profile-container .settings {
  padding: 15px;
}

.profile-container .settings p {
  font-family: arial;
  font-size: 14px;
  color: #f1f1f1;
  margin: 0px;
}

@media only screen and (max-width: 790px) {
  .navbar-box .navbar-middle {
    justify-content: flex-end;
  }
  .navbar-box .navbar-middle .search-box {
    align-items: center;
  }
  .navbar-box .navbar-middle .search-box div .search {
    display: none;
  }
  .navbar-box .navbar-middle .search-box div .search-icon {
    width: 50%;
    background-color: transparent;
    border: none;
  }
  .tab-container .all {
    margin-left: 16px;
  }
}

@media only screen and (max-width: 620px) {
  .navbar-box .navbar-middle {
    justify-content: center;
  }
  .navbar-box .navbar-middle .search-box {
    width: 5px;
  }
  .navbar-box .navbar-middle .search-box div .search-icon {
    margin-right: 20px;
    border: none;
  }
  .navbar-box .navbar-middle img {
    display: none;
  }
}

@media only screen and (max-width: 400px) {
  .navbar-box .navbar-middle {
    justify-content: flex-end;
    padding-right: 25px;
  }
  .navbar-box .navbar-right {
    width: 50px;
  }
  .navbar-box .navbar-right img {
    display: none;
  }
  .navbar-box .navbar-right .profile {
    margin-right: 15px;
    display: flex;
  }
}
